import { ChevronLeftIcon, ChevronRightIcon } from "@heroicons/react/24/outline";
import { ITEMS_PER_PAGE } from "../../app/constants";

export default function Pagination({ page, setPage, handlePage, totalItems }) {
  const totalPages = Math.ceil(totalItems / ITEMS_PER_PAGE);

  const handlePageChange = (newPage) => {
    handlePage(newPage);
    window.scrollTo({ top: 0, behavior: 'smooth' });
  };

  return (
    <div className="flex items-center justify-between border-t border-gray-200 bg-white px-4 py-3 sm:px-6">
      <div className="flex flex-1 justify-between sm:hidden">
        <div
          onClick={(e) => handlePageChange(page > 1 ? page - 1 : page)}
          className={`relative inline-flex items-center rounded-md border px-4 py-2 text-sm font-medium   ${
            page > 1
              ? "text-white cursor-pointer bg-custom-gradient border-blue-700 hover:bg-blue-700"
              : "bg-gray-200 text-gray-400 cursor-not-allowed  border-gray-400"
          }`}
        >
          Previous
        </div>
        <div
          onClick={(e) => handlePageChange(page < totalPages ? page + 1 : page)}
          className={`relative inline-flex items-center rounded-md border px-4 py-2 text-sm font-medium ${
            page < totalPages
              ? "text-white cursor-pointer bg-custom-gradient  border-blue-700 hover:bg-blue-700"
              : "bg-gray-200 text-gray-400 cursor-not-allowed  border-gray-400"
          }`}
        >
          Next
        </div>
      </div>
      <div className="hidden sm:flex sm:flex-1 sm:items-center sm:justify-between">
        <div>
          <p className="text-sm text-gray-700">
            Showing{" "}
            <span className="font-medium">
              {(page - 1) * ITEMS_PER_PAGE + 1}
            </span>{" "}
            to{" "}
            <span className="font-medium">
              {page * ITEMS_PER_PAGE > totalItems
                ? totalItems
                : page * ITEMS_PER_PAGE}
            </span>{" "}
            of <span className="font-medium">{totalItems}</span> results
          </p>
        </div>
        <div>
          <nav
            className="isolate inline-flex -space-x-px rounded-md shadow-sm"
            aria-label="Pagination"
          >
            <div
              className={`relative inline-flex items-center rounded-l-md px-2 py-2  ring-1 ring-inset ring-gray-300 hover:bg-gray-50 focus:z-20 focus:outline-offset-0 ${
                page > 1
                  ? "text-gray-900 cursor-pointer"
                  : "text-gray-400 cursor-not-allowed"
              }`}
              onClick={(e) => handlePageChange(page > 1 ? page - 1 : page)}
            >
              <span className="sr-only">Previous</span>
              <ChevronLeftIcon className="h-5 w-5" aria-hidden="true" />
            </div>
            {/* Current: "z-10 bg-blue-800 text-white focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-blue-800", Default: "text-gray-900 ring-1 ring-inset ring-gray-300 hover:bg-gray-50 focus:outline-offset-0" */}

            {Array.from({ length: totalPages }).map((el, index) => (
              <div
                key={index}
                onClick={(e) => handlePageChange(index + 1)}
                aria-current="page"
                className={`relative cursor-pointer z-10 inline-flex items-center ${
                  index + 1 === page
                    ? "bg-custom-gradient text-white"
                    : "text-gray-400"
                } px-4 py-2 text-sm font-semibold  focus:z-20 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-blue-800`}
              >
                {index + 1}
              </div>
            ))}

            <div
              onClick={(e) => handlePageChange(page < totalPages ? page + 1 : page)}
              className={`relative inline-flex items-center rounded-r-md px-2 py-2 ring-1 ring-inset ring-gray-300 hover:bg-gray-50 focus:z-20 focus:outline-offset-0 ${
                page < totalPages
                  ? "text-gray-900 cursor-pointer"
                  : "text-gray-400 cursor-no-drop"
              }`}
            >
              <span className="sr-only">Next</span>
              <ChevronRightIcon className="h-5 w-5" aria-hidden="true" />
            </div>
          </nav>
        </div>
      </div>
    </div>
  );
}
